<template>
    <div>
      <el-dialog title="更换工单上报" width='90%' :visible.sync="dialogTableVisible" class="dialog">
            <div class = 'inner'>
                <el-form label-position="left"  :model="form" :rules="rules"  ref="form" label-width="130px">
                    <div class="flex">
                        <div class="label">原工单分配：</div>
                        <div class="flex_1">{{typeVO.title}}</div>
                    </div>
                    <el-form-item label="调整上报原因：" prop="uploadReason">
                        <div class='flex_1'><el-input v-model="form.uploadReason" placeholder="请输入"></el-input></div>
                    </el-form-item>
                    <el-form-item label="调整更换工单：" prop="intendTemplateId">
                        <el-select v-model="form.intendTemplateId" placeholder="请选择">
                            <el-option
                                v-for="item in options"
                                :key="item.id"
                                :label="item.title"
                                :value="item.id">
                            </el-option>
                        </el-select>
                      <img :src="preview"  alt="预览" style="margin-left: 10px" @click="previewWorkOrder">
                    </el-form-item>
                    <div  class="Qfooter">
                        <el-button @click='dialogTableVisible=false'>取 消</el-button>
                        <el-button type="primary" @click="submitFn">上报</el-button>
                    </div>
                </el-form>
            </div>
        </el-dialog>
      <See ref="see"></See>
    </div>

</template>
<script>
import request from '@/lib/axios.js';
import preview from '@/assets/img/preview.svg'
import See from "./see";
export default {
  components: {See},
  props: ['airData','typeVO'],
    data() {
        return {
            preview,
            dialogTableVisible: false,
            options: [],
            uploadReason: '',
            intendTemplateId:'',
            form: {},
            rules: {
                name: [{ required: true, message: "请输入订阅名称", trigger: "change" }],
                uploadReason: [{ required: true, message: "请输入原因", trigger: "blur" }],
                intendTemplateId: [{ required: true, message: "请选择要更换的工单", trigger: "change" }],
            }
        }
    },
    mounted(){

    },
    methods:{
        init(){
            this.form = {};
            this.getOptionFn()
        },
        getOptionFn(){
            this.options = []
            request({
                url:`${this.$ip}/mms-workorder/app/workorder/getOption`,
                method: 'get',
                params:{
                    templateId:this.airData.templateId
                }
            })
            .then((data) => {
                console.log(data)
                if(data.code == 200){
                   this.options = data.data
                }else{
                    this.$message({type: 'error', message: '获取下拉接口失败，请重试'});
                }
            })
        },
        open(){
            this.dialogTableVisible = true
            this.uploadReason = ''
            this.intendTemplateId = ''
            this.init()
        },
        submitFn(){
            this.$refs.form.validate(valid => {
                if(valid){
                    request({
                        url:`${this.$ip}/mms-workorder/changeReport/report`,
                        method: 'post',
                        data:{
                            "workorderId":this.airData.id,//新增
                            "originTemplateId":this.airData.templateId,
                            "intendTemplateId":this.form.intendTemplateId,
                            "reason":this.form.uploadReason
                        }
                    })
                    .then((data) => {
                        console.log(data)
                        if(data.code == 200){
                            this.$message({type: 'success', message: '提交成功'});
                            this.$emit('refreshFn')
                            this.dialogTableVisible = false
                        }else{

                            this.$message({type: 'error', message: data.message});
                        }
                    });
                }
            });
        },
      previewWorkOrder(){
          if (this.form.intendTemplateId) {
            this.$refs.see.showDialog(this.form.intendTemplateId);
          }else {
            this.$message.warning("请先选择一个工单");
          }
      }
    }
}
</script>
<style scoped lang='less'>
    .dialog{
        .inner{
            color:#333333;
            line-height: 58px;
            .label{
                width:140px;
            }
            .el-form-item{
                /deep/ .el-select{
                    width:90%;
                }
            }
            .dialog-footer{
                text-align: center;
            }

        }
        .Qfooter{
            margin-top: 15px;
        }
     /deep/ .el-dialog__body{
        padding-bottom: 15px;
      }
    }
</style>



