<template>
	<div>
		<el-dialog title="提示" width="80%" :close-on-press-escape="false" :show-close="false"
				   :visible.sync="dialogInvalidVisible">
			<p>
				<i class="el-icon-warning-outline"></i>该工单已完成，是否需要新增工单？
			</p>
			<div slot="footer" class="dialog-footer">
				<el-button @click="close">否</el-button>
				<el-button @click='submitInvalidFormFn( )' type='primary'>是</el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
import request from '@/lib/axios.js';

export default {
	name: "promptFinishDialog",
	components: {},
	data() {
		return {
			id: '',
			clicking:false,
			dialogInvalidVisible: false
		}
	},
	methods: {
		close() {
			this.id = ''
			this.dialogInvalidVisible = false
		},
		open(id) {
			this.id = id
			this.clicking=false
			this.dialogInvalidVisible = true
		},
		submitInvalidFormFn(form) {
			if(this.clicking){
				return false
			}
			this.clicking=true
			// 	返回 字符串，新工单ID
			request({
				url: `${this.$ip}/mms-workorder/app/workorder/reCreate?id=${this.id}`,
				method: 'get',
			}).then((d) => {
				if (d.code == 200) {
					this.$emit('getResult', d.data)
					this.close()
				} else {
					this.clicking=false
					this.$message.warning(d.message)
				}
			}).catch(() => {
				this.clicking=false
				// this.$message.info('保存原因失败')
			})
		}
	},

}
</script>

<style lang="less" scoped>
 p{
	 font-size: 20px;
	 text-align: center;
	 &>i{
		 margin: 0 5px;
		 color: #d98d09;
	 }

 }

 /deep/ .dialog-footer{
	 button{
		 padding: 5px 20px;
	 }
	 span{
		 font-size: 18px;
	 }
 }
</style>


