<template>
	<div class="index">
		<div class="top_header flex justify_between">
			<div><!--{{'<'}}--></div>
			<div>工单分派匹配</div>
			<div></div>
			<div class="iconSvg">
				<icon-svg iconClass="usericon"></icon-svg>
				{{ userName }}{{ workNumber ? `(${workNumber})` : '' }}
			</div>

		</div>
		<div style="height: 68px"></div>
		<div class="content">
			<div>
				<table border="1" cellpadding="0" cellspacing="0" class="table">
					<tr>
						<td>
							<div>工作日期</div>
						</td>
						<td>
							<div>执行单位</div>
						</td>
						<td>
							<div>机位</div>
						</td>
						<td>
							<div>状态</div>
						</td>
					</tr>
					<tr>
						<td>
							<div>
								{{
									airData.workTime
										? moment(airData.planDepartureTime).format(
											"YYYY-MM-DD HH:mm"
										)
										: "--"
								}}
							</div>
						</td>
						<td>
							<div>{{ airData.department || "--" }}</div>
						</td>
						<td>
							<div>{{ airData.seat || "--" }}</div>
						</td>
						<td>
							<div>{{ airData.flightState || "--" }}</div>
						</td>
					</tr>
					<tr>
						<td>
							<div>航班号</div>
						</td>
						<td>
							<div>航空公司</div>
						</td>
						<td>
							<div>机型</div>
						</td>
						<td>
							<div>类型</div>
						</td>
					</tr>
					<tr>
						<td>
							<div>{{ airData.flightNo || "--" }}</div>
						</td>
						<td>
							<div>{{ airData.airlineCompanyName || "--" }}</div>
						</td>
						<td>
							<div>{{ airData.airplaneIcao || "--" }}</div>
						</td>
						<td>
							<div>{{ airData.bodySize || "--" }}</div>
						</td>
					</tr>
					<tr>
						<td>
							<div>
								操作程序<span style="color:#7F7F7F">（仅针对勤务项目）</span>
							</div>
						</td>
						<td>
							<div>
								操作程序<span style="color:#7F7F7F">（仅针对桥载项目）</span>
							</div>
						</td>
						<td>
							<div>计划进港时间</div>
						</td>
						<td>
							<div>计划出港时间</div>
						</td>
					</tr>
					<tr>
						<td>
							<div>
								<el-select
									:disabled="!qwOptions.find(i => i.value === typeVO.type)"
									v-model="qwValue"
									placeholder="请选择"
									@change="valueChangeFn"
								>
									<el-option
										v-for="item in qwOptions"
										:key="item.valCode"
										:label="item.valData"
										:value="item.valCode"
									>
									</el-option>
								</el-select>
							</div>
						</td>
						<td>
							<div>
								<el-select
									:disabled="typeVO.type!=='QZGD'"
									v-model="qzValue"
									placeholder="请选择"
									@change="valueChangeFn"
								>
									<el-option
										v-for="item in qzOptions"
										:key="item.valCode"
										:label="item.valData"
										:value="item.valCode"
									>
									</el-option>
								</el-select>
							</div>
						</td>
						<td>
							<div>
								{{
									airData.planArrivalTime
										? moment(airData.planDepartureTime).format(
											"YYYY-MM-DD HH:mm"
										)
										: "--"
								}}
							</div>
						</td>

						<td>
							<div>
								{{
									airData.planDepartureTime
										? moment(airData.planDepartureTime).format(
											"YYYY-MM-DD HH:mm"
										)
										: "--"
								}}
							</div>
						</td>
					</tr>
				</table>
			</div>
			<div class="con_up" v-if="type != 1">
				<div><span v-if="typeVO.airlineCompany">{{ typeVO.airlineCompany }}-</span>{{ typeVO.title }}</div>
				<div class="flex">
					<div class="label">分派匹配工单：</div>
					<div>
						工单流水编号-{{ airData.serialNo }}
						<img
							:src="preview"
							alt="预览"
							style="margin-left: 20px"
							@click="previewWorkOrder"
						/>
					</div>
				</div>
				<div class="flex">
					<div class="label">参考工时：</div>
					<div>3小时</div>
				</div>
				<div class="flex">
					<div class="label">当前状态:</div>
					<div>{{ airData.stateCn }}</div>
				</div>
			</div>
			<div class="con_up" v-if="type == 1">
				<div>工单更换中，请等待队长审核通过</div>
				<div class="flex">
					<div class="label">原分配工单：</div>
					<div>{{ changeReportList.originTemplateTitle }}</div>
				</div>
				<div style="padding:8px 0">
					<img
						src="@/assets/img/shuangjiantou.svg"
						style="width:26px;"
						alt=""
					/>
				</div>
				<div class="flex">
					<div class="label">调整更换工单：</div>
					<div>{{ changeReportList.intendTemplateTitle }}</div>
				</div>
			</div>
			<div class="btn_group flex justify_center ">
				<el-button v-if="type == 1" @click="refreshFn">刷新</el-button>
				<el-button @click="openChangeOrderDialogFn">更换工单上报</el-button>
				<el-button
					type="primary"
					@click="routerPush('/orderSign', { id: airData.id }, enableGis)"
				>执行工单
				</el-button
				>
			</div>
		</div>
		<promptFinishDialog @getResult="getResult" ref="promptFinishDialog"></promptFinishDialog>
		<change-order-dialog
			ref="changeOrderDialog"
			:airData="airData"
			:typeVO="typeVO"
			@refreshFn="refreshFn"
		></change-order-dialog>
		<See ref="see"></See>
	</div>
</template>
<script>
import {getUserInfo} from '@/lib/auth.js';

import changeOrderDialog from "./changeOrderDialog";
import request from "@/lib/axios.js";
import Bridge from "@/assets/js/jsBriage.js";
import merge from "webpack-merge";
import moment from "moment";
import preview from "@/assets/img/preview.svg";
import See from "./see";
import promptFinishDialog from "./promptFinishDialog";

export default {
	components: {
		See,
		changeOrderDialog, promptFinishDialog
	},
	data() {
		return {
			userName: getUserInfo().name,
			workNumber: getUserInfo().workNumber,

			preview,
			moment,
			airData: {
				id: "" //新增
			},
			type: "0", // 1 为提交中，2为刷新中
			typeObj: {
				"1": "该工单上报中，请稍后执行",
				"3": "该工单已完成",
				"4": "该工单已无效",
				"5": "该工单已完成"
			},
			typeVO: {},
			changeReportList: {},
			qwValue: "",
			qwOptions: [],
			qzOptions: [],
			qzValue: "",

			showPreview: false,
			enableGis: true
		};
	},
	created() {
		if (this.$route.query) {
			if (this.$route.query.id) {
				this.airData.id = this.$route.query.id;
			} else {
				this.airData.id = this.$route.query.workOrderId;
			}
		}
		request({
			url: `${this.$ip}/mms-parameter/businessDictionaryValue/listByCodes`,
			method: 'post',
			params: {delete: false},
			data: ['qwOptions', 'QZ_workType']
		}).then(d => {
			if (d.data) {
				this.qwOptions = d.data.qwOptions
				this.qzOptions = d.data.QZ_workType
			}

		});
		//WXGD 维修工单
		this.refreshFn();
		//查询是否需要进行gis定位检查
		request({
			url: `${this.$ip}/mms-parameter/rest-api/sysParam/info`,
			method: "post",
			data: {
				id: "176a8ada7c4a453f9b429044827b0aed"
			}
		})
			.then(data => {
				if (data.code == 200) {
					//数据为1是开启，0是关闭
					this.enableGis = data.data[0].sysParamValue == 1;
				} else {
					console.log("查询GIS配置开关失败，请检查系统参数！");
				}
			})
			.catch(res => {
				console.log("后台接口error");
			});
	},
	methods: {
		openChangeOrderDialogFn() {
			if (this.airData.type != "WXGD") {
				this.$message({type: "warning", message: "请在操作程序中修改"});
				return;
			}
			//2020.10.19 维修工单允许上报
			const wxFlag = this.airData.type === "WXGD";
			if (this.type == 0 || wxFlag) {
				if (this.type == 2) {
					this.$confirm(
						"工单执行中，是否申请更换，更换后原有工单将会废止",
						"提示",
						{
							confirmButtonText: "确定",
							cancelButtonText: "取消"
						}
					)
						.then(d => {
							this.$refs.changeOrderDialog.open();
						})
						.catch(() => {
							this.$message.info("已取消操作");
						});
				} else {
					this.$refs.changeOrderDialog.open();
				}
			} else {
				this.$message({type: "warning", message: "该工单目前不能修改"});
			}
		},
		getResult(id) {
			this.$router.push({
				query: merge(this.$route.query, {
					workOrderId: id, id
				})
			});
			// isVerifyState
			this.routerPush('/orderSign', {id: id}, this.enableGis,false)
		},
		refreshFn() {
			this.qwValue = "";
			this.qzValue = "";
			request({
				url: `${this.$ip}/mms-workorder/app/workorder/refresh?id=${this.airData.id}`,
				method: "get"
			}).then(data => {
				if (data.code == 200) {
					/**
					 * 工单状态
					 */
					// PENDING(0, "待执行"), AUDITING(1, "上报中"), WORKING(2, "作业中"),
					// COMPLETE(3, "完成"), INVALID(4, "无效");
					this.type = data.data.workorder.state;
					// 桥载已完成工单 需要提示 是否新增工单
					if (this.type == 3 && data.data.workorder.type.startsWith("QZ")) {
						this.$refs.promptFinishDialog.open(this.airData.id)
					}
					this.airData = data.data.workorder;
					this.typeVO = data.data.template.typeVO;
					if (this.qwOptions.find(i => i.value === this.typeVO.type)) {
						this.qwValue = this.typeVO.type;
					}
					if (this.qzOptions.find(i => i.valCode === this.typeVO.jobType)) {
						this.qzValue = this.typeVO.jobType;
					}
					this.changeReportList =
						data.data.changeReportList && data.data.changeReportList[0];
					//修改原有参数
					this.$router.push({
						query: merge(this.$route.query, {
							id: `${this.airData.id}`
						})
					});
				} else {
					this.$message({type: "error", message: JSON.stringify(data)});
				}
			});
		},
		async routerPush(path, query = {}, isVerify = false,isVerifyState=true) {
			// isVerifyState 默认校验工单状态 ,当桥载工单新增后（当前页面未更新数据）跳转不需要校验
			if (this.typeObj[this.type] != undefined&&isVerifyState) {
				if (this.type == 3 && this.airData.type == 'QZGD') {
					// this.$confirm(`该工单已完成，是否需要新增工单？`,'提示',{
					// 	confirmButtonText:'是',
					// 	cancelButtonText:'否',
					// 	type:'warning'
					// }).then((d)=>{
					//
					// }).catch((e)=>{
					// 	this.$message.info('已取消操作')
					// })
					this.$refs.promptFinishDialog.open(this.airData.id)
				} else {
					this.$message.info(this.typeObj[this.type]);
				}
				return false;
			}

			console.log("gis开关参数是：" + isVerify);
			if (isVerify) {
				// 需要验证位置
				let u = navigator.userAgent;
				let isAndroid = u.indexOf("Android") > -1 || u.indexOf("Linux") > -1; //g
				let isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
				if (isAndroid) {
					//这个是安卓操作系统
					if (await this.checkGisFn()) {
						this.$router.push({path, query});
					}
				} else if (isIOS) {
					//这个是ios操作系统
					this.$router.push({path, query});
				}
			} else {
				// 不需要验证位置
				this.$router.push({path, query});
			}
		},
		checkGisFn() {
			let _this = this;
			let msg = "333";
			return new Promise((resolve, reject) => {
				Bridge.callHandler("gpsInfo", msg, res => {
					request({
						url: `${_this.$ip}/mms-parameter/rest-api/electronicFence/queryByParkingNo`,
						method: "post",
						data: {
							parkingNo: this.airData.seat
						}
					})
						.then(parkingInfo => {
							if (parkingInfo.code == 200) {
								request({
									url: `${_this.$ip}/mms-workorder/app/workorder/checkGis`,
									method: "post",
									data: {
										longitude: JSON.parse(res).longitude.toString(),
										latitude: JSON.parse(res).latitude.toString(),
										maxError: parkingInfo.data.maxError,
										parkingNo: this.airData.seat
									}
								})
									.then(data => {
										if (data.code == 200 && data.data) {
											if (data.data) {
												resolve(true);
											} else {
												_this.$message({
													type: "info",
													message: "当前不在目标范围内"
												});
												resolve(false);
											}
										} else {
											_this.$message({type: "warn", message: data.data});
											resolve(false);
										}
									})
									.catch(res => {
										_this.$message({
											type: "error",
											message: JSON.stringify(res)
										});
										resolve(false);
									});
							}
						})
						.catch(res => {
							_this.$message({type: "error", message: JSON.stringify(res)});
							resolve(false);
						});
				});
			});
		},
		valueChangeFn(val) {
			this.changeByProcessFn(val);
		},
		changeByProcessFn(val) {
			request({
				url: `${this.$ip}/mms-workorder/app/workorder/changeByProcess`,
				method: "get",
				params: {
					id: this.airData.id,
					process: val
				}
			}).then(data => {
				console.log(data);
				if (data.code == 200) {
				} else {
					this.$message({type: "error", message: data.message});
				}
				this.refreshFn();
			});
		},
		previewWorkOrder() {
			this.$refs.see.showDialog(this.typeVO.id);
		}
	}
};
</script>
<style scoped lang="less">
.index {
	padding: 30px;

	.top_header {
		line-height: 68px;
		height: 68px;
		position: fixed;
		font-size: 24px;
		left: 0;
		top: 0;
		right: 0;
		background: #2d4157;
		color: #fff;
		padding: 0 10px;
		z-index: 99;
	}

	.content {
		.table {
			width: 100%;

			// border:1px solid #d9d9d9;
			tr {
				td {
					padding: 10px 5px;

					.com_flag {
						line-height: 20px;
						padding: 0 5px;
					}

					line-height: 26px;
					text-align: center;
					font-size: 18px;

					&:nth-of-type(1) {
						width: 170px;
						text-align: center;
						line-height: 26px;
					}

					&:nth-of-type(2) {
						width: 170px;
						text-align: center;
						line-height: 26px;
					}

					.sign_box {
						height: 100%;
					}
				}
			}

			td {
				line-height: 56px;
				padding: 6px;
			}
		}

		.btn_group {
			margin-top: 20px;
		}
	}

	.con_up {
		margin-top: 20px;
		border: 1px solid rgb(128, 128, 128);
		padding: 20px;
		line-height: 36px;
		font-size: 24px;

		.label {
			width: 180px;
		}
	}
}
</style>



